
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#balance .cash {
  background: #fff;
}
#balance .cash > div {
  overflow: hidden;
  padding: 10px 20px;
  border-bottom: 1px solid #ddd;
}
#balance .cash > div .wd100 {
  width: 100px;
}
#balance .cash > div .fcol {
  color: #999;
}
#balance .cash .cash-num .fl .num input {
  border: none;
  font-size: 16px;
}
#balance .cash .cash-num .fl .num input:focus {
  outline: none;
}
#balance .cash .cash-num .fl p {
  margin-top: 5px;
}
#balance .smit {
  margin-top: 20px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background: #04b101;
  color: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 20px 0;
}



